import React, { useState, useEffect } from 'react';
import { 
  // Flex, 
  Typography, 
  DisplayText, 
  SectionHeading, 
  Paragraph, 
  RadioButtonField,
  TextInput,
  Textarea,
  FieldGroup, 
  Spinner,
  Notification,
  Button,
  SkeletonContainer,
  SkeletonImage,
  Modal,
  Card,
  EmptyState,
  List,
  Pill,
  Icon,
  HelpText,
  Dropdown,
  DropdownList,
  DropdownListItem,
  // Autocomplete 
} from '@contentful/forma-36-react-components';
import { EditorExtensionSDK } from '@contentful/app-sdk';
import '@contentful/forma-36-react-components/dist/styles.css';
import '../index.css';
import { useDropzone } from 'react-dropzone';
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd';

const thumb = {
  display: 'inline-flex',
  borderRadius: 2,
  border: '1px solid #eaeaea',
  marginBottom: 8,
  marginRight: 8,
  // width: 100,
  // height: 100,
  // width: 200,
  height: 200,
  padding: 4,
  // boxSizing: 'border-box'
};

const thumbInner = {
  display: 'flex',
  minWidth: 0,
  overflow: 'hidden'
};

const imgStyle = {
  display: 'block',
  width: 'auto',
  height: '100%'
};


interface EditorProps {
  sdk: EditorExtensionSDK;
}

// for the inital release version
