// }

// Ganado en Venta


// const CONTENT_FIELD_NAME:string = 'name';
// const CONTENT_FIELD_ID:string = 'onSale'; // boolean value
// const CONTENT_AVATAR:string = 'image';
// const CONTENT_FIELD_BODY:string = 'body';
// const CONTENT_FIELD_GALLERY:string = 'multImg';
